import BaseValidator from 'validate-framework-utils';
import {
  getCurrentEvent,
  isSelect,
  isSameNameField,
  fieldOtherInitProps,
  assembleField,
} from './util';
import testHook from './testHook';

/**
 * 
 */
export default class Core extends BaseValidator {

  constructor(options) {
    super();
    // 
    Object.assign(this, testHook);

    // 
    if (!options) {
      return this;
    }

    // params
    this.opts = Object.assign({
      classNames: {
        error: 'valid-error',
        success: 'valid-success',
      },
    }, options);

    // init
    this.form = {};
    this.errors = {};
    this.fields = {};
    this.handles = {};

    // 
    this.addFields(this.opts.fields);

    //  form 
    if (this.opts.formName) {
      // 
      this.form = document.forms[this.opts.formName];
      //  novalidate
      this.form.setAttribute('novalidate', 'novalidate');
      // 
      this.addFormEvent();
      // 
      this.handleOnSubmit();
    }
    return this;
  }

  /**
   * 
   * @param  {Event} evt 
   * @return {Boolean} 
   */
  validate(evt) {
    this.handles.evt = getCurrentEvent(evt);
    let isSuccess = true;

    Object.keys(this.fields).forEach((name) => {
      //  name 
      if (!this.validateByName(name)) {
        isSuccess = false;
      }
    });

    //  submit 
    if (!isSuccess) {
      this.preventSubmit();
    }

    // 
    if (typeof this.opts.callback === 'function') {
      this.opts.callback(isSuccess, this.errors);
    }

    return isSuccess;
  }

  /**
   * 
   * @param {String} name 
   * @return {Boolean}
   */
  validateByName(name) {
    const field = this.fields[name];
    let isSuccess = true;

    // 
    if (!field) {
      return !isSuccess;
    }

    //  DOM 
    const elArray = this.handleGetArrayByName(field.name);

    //  name  radiocheckboxselect 
    if (isSameNameField(elArray)) {
      elArray.forEach((item) => {
        //  field 
        //  el 
        field.el = [item];
        if (!this.assembleValidateField(field)) {
          isSuccess = false;
        }
      });
    } else {
      // 
      field.el = elArray;
      isSuccess = this.assembleValidateField(field);
    }

    return isSuccess;
  }

  /**
   * 
   * @param field
   * @return {Boolean}
   */
  assembleValidateField(field) {
    // 
    assembleField(field);
    // 
    const { result, error } = this.validateByField(field);
    // 
    error.el = field.el;
    this.errors[error.name] = result ? null : error;
    // 
    this.afterFieldValidate(result, error);

    return result;
  }

  /**
   * 
   */
  preventSubmit() {
    const evt = this.handles.evt;

    if (evt && evt.preventDefault) {
      evt.preventDefault();
    } else if (evt) {
      // IE 
      evt.returnValue = false;
    }

    return this;
  }

  /**
   *  fields 
   * @param {Object} fields
   */
  addFields(fields) {
    if (typeof fields === 'object') {
      // 
      Object.keys(fields).forEach((name) => {
        // 
        this.fields[name] = {
          ...fields[name],
          name,
          ...fieldOtherInitProps,
        };
      });
    }
    return this;
  }

  /**
   *  fields 
   * @param {string} names 
   */
  removeFields(...names) {
    names.forEach((name) => {
      // 
      delete this.fields[name];
      delete this.errors[name];
    });
    return this;
  }

  /**
   *  submit 
   */
  handleOnSubmit() {
    const thatOnSubmit = this.form.onsubmit;
    this.form.onsubmit = (that => (e) => {
      try {
        const evt = getCurrentEvent(e);
        return that.validate(evt) && (thatOnSubmit === undefined || thatOnSubmit());
      } catch (ex) {
        return null;
      }
    })(this);
  }

  /**
   *  nodeList  Array 
   * @param {String} name 
   * @return {Array}
   */
  handleGetArrayByName(name) {
    // field element
    const el = this.form[name];
    const result = [];

    // 
    if (!el || el.length === 0) {
      return result;
    }

    // 
    const elLength = el.length;

    //  select select 
    if (elLength && !isSelect(el)) {
      for (let i = 0; i < elLength; i += 1) {
        result.push(el[i]);
      }
    } else {
      result.push(el);
    }
    return result;
  }
}



